body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: rgb(252, 135, 135);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1,
h6 {
  display: inline;
  cursor: default;
}

h1 {
  font-size: 5em;
}

h6 {
  font-size: 1.5em;
}

h1:hover {
  color: #fff;
  display: inline;
}

h6:hover {
  color: #fff;
  background-color: rgba(255,115,115,1) 0%;
  display: inline;
}

.scoreB {
  margin-bottom: 10px;
}

.col-left {
  font-family: 'Faster One', cursive;
  text-align: left;
}

.col-right {
  font-family: 'Faster One', cursive;
  text-align: right;
  padding-top: 20px;
  padding-right: 40px;
}

.alert-danger {
  font-family: 'Faster One', cursive;
  text-transform: uppercase;
  color: #fff;
  /* display: inline; */
  background-color: red !important;
  border-radius: 0 !important;
  border-color: none !important;
  border: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  cursor: default;
  /* margin: auto; */
}

.alert-danger:hover {
  color: #fff;
  background-color: #000 !important;
}

.alert-success {
  font-family: 'Faster One', cursive;
  text-transform: uppercase;
  color: #fff;
  /* display: inline; */
  background-color: blue !important;
  border-radius: 0 !important;
  border-color: none !important;
  border: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  cursor: default;
}

.alert-success:hover {
  color: #fff;
  background-color: rgb(0, 0, 0) !important;
}

#root {
  background: rgb(255,115,115);
background: linear-gradient(90deg, rgba(255,115,115,1) 0%, rgba(255,157,156,1) 35%, rgba(255,246,246,1) 100%);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: -30px;
}
iframe {
  width: 100%;
  height: 0px;
  background-color: #fff;
}
.footer {
  font-family: 'Faster One', cursive;
  background-color: #000;
  color: #fff;
  text-align: right;
  /* height: 400px; */
}
.footer:hover {
  background-color: #fff;
  color:rgb(0, 0, 0);
}
.footer a {
  color: rgba(255,115,115,1) 0%;
}
.footer a:hover {
  color:rgba(255,115,115,1) 0%;
  text-decoration: none;
}
.card {
    font-size: 10px;
    height: 250px;
    width: 250px;
    margin: 0 auto 30px;
    text-align: center;
    border: none !important;
    background: none;
    background-repeat: no-repeat;
  }
  
  .card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  } 
  
  .card .img-container > img {
    width: 100%;
    height: 100%;
  }
  
  .overlay {
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: #000;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: 0.5s ease;
    opacity: 0.8;
    cursor: pointer;
    cursor:cell;
    cursor:crosshair; 
    cursor:progress; 
  }
  
  .text {
    font-family: "Noto Serif SC", serif;
    color: white;
    font-size: 1.8em;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    white-space: nowrap; 
  }
